<template>
  <div class="modules">
    <div class="title" :style="randomColor">
      {{ name }}
    </div>
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: ["name", "color"],
  computed: {
    randomColor() {
      return { color: this.color };
    },
  },
};
</script>

<style lang="less">
.modules {
  width: 100%;
  margin-bottom: 20px;
  background-color: #1f1f1f;
  border-radius: 3px;
  border: 1px solid ;
  .content{
    // padding: 30px 15px 20px 15px;
  }
  .title {
    padding: 10px 15px;
    background-color: #191818;
    color: lavender;
  }
}
</style>
