<template>
  <div class="container">
    <div class="left"><router-view /></div>
    <div class="right">
      <ModuleList name="随机云标签" :color="color" class="tags">
        <a @click="tagClick(item.id)" v-for="(item, index) in tags" :key="index"
          ><span :style="{ color: randomColor(), fontSize: randomSize() }">{{
            item.tag
          }}</span></a
        >
      </ModuleList>
      <ModuleList name="最近热门" class="hot" :color="color">
        <ul>
          <li v-for="(item, index) in hot" :key="index">
            <a :href="'/details/' + item.id">{{ item.title }}</a>
          </li>
        </ul>
      </ModuleList>
      <ModuleList name="最新评论" :color="color" class="comment">
        <ul>
          <li v-for="(item, index) in comment" :key="index">
            <p>
              <span>{{ item.userName }}</span
              ><span>{{ item.ctime }}</span>
            </p>
            <a :href="herf(item)">{{ item.content }}</a>
          </li>
        </ul>
      </ModuleList>
      <ModuleList
        name="友情链接"
        :color="color"
        class="link"
        v-if="$route.path === '/'"
      >
        <ul>
          <li v-for="(link, index) in links" :key="index">
            <a :href="link.link" :linkId="link.id">{{ link.name }}</a>
          </li>
        </ul>
      </ModuleList>
    </div>
  </div>
</template>

<script>
import ModuleList from "../compins/ModuleList";
import { socll } from "../../utils/index";
export default {
  data() {
    return {
      timer: null,
      color: null,
      tags: [],
      hot: [],
      comment: [],
      links: [],
    };
  },
  mounted() {
    this.timer = setInterval(() => {
      let r = Math.random() * (255 - 50) + 50;
      let g = Math.random() * (255 - 50) + 50;
      let b = Math.random() * (255 - 50) + 50;
      this.color = `rgb(${r},${g},${b})`;
    }, 5000);
    // 请求友情链接
    fetch("/api/link")
      .then((resp) => resp.json())
      .then((resp) => {
        if (resp.code === 0) {
          this.links = resp.data;
        }
      });
    // 请求标签
    fetch("/api/tags")
      .then((resp) => resp.json())
      .then((resp) => {
        if (resp.code === 0) {
          this.tags = resp.data;
        }
      });
    // 请求热门文章
    fetch("/api/blog/hot", {
      method: "post",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ limit: 10, order: [["views", "DESC"]] }),
    })
      .then((resp) => resp.json())
      .then((resp) => {
        if (resp.code === 0) {
          this.hot = resp.data;
        }
      });

    // 请求热门评论的
    fetch("/api/comment?page=1")
      .then((resp) => resp.json())
      .then((resp) => {
        this.comment = resp.data.data;
      });
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  computed: {
    randomSize() {
      return () => {
        return Math.random() * (24 - 12) + 12 + "px";
      };
    },
    randomColor() {
      return () => {
        let r = Math.random() * (255 - 50) + 50;
        let g = Math.random() * (255 - 50) + 50;
        let b = Math.random() * (255 - 50) + 50;
        return `rgb(${r},${g},${b})`;
      };
    },
  },
  methods: {
    // randomSize() {
    //   return Math.random() * (24 - 12) + 12 + "px";
    // },
    // randomColor() {
    //   let r = Math.random() * (255 - 50) + 50;
    //   let g = Math.random() * (255 - 50) + 50;
    //   let b = Math.random() * (255 - 50) + 50;
    //   return `rgb(${r},${g},${b})`;
    // },
    tagClick(id) {
      this.$store.dispatch("setTagBlog", id);
      socll();
    },
    herf(item) {
      if (item.blogid == -1) {
        return "/about";
      } else if (item.blogid == -2) {
        return "/guestbook";
      } else {
        return "/details/" + item.blogid;
      }
    },
  },
  components: {
    ModuleList,
  },
};
</script>

<style lang="less">
.container {
  width: 1170px;
  margin: 20px auto;
  &::after {
    display: block;
    content: "";
    clear: both;
  }
  .left {
    width: 66%;
    box-sizing: border-box;
    padding-right: 20px;
    float: left;
  }
  .right {
    width: 34%;
    float: left;
    .comment {
      .content {
        padding: 5px 0 0 0;
      }
      font-size: 14px;
      li {
        // height: 40px;
        border-bottom: 1px solid #292929;
        padding: 5px 15px;
        // line-height: 40px;
        p {
          color: #fff;
          font-weight: 600;
          display: flex;
          justify-content: space-between;
          margin-bottom: 5px;
        }
        a {
          text-decoration: none;
          color: #777;
          margin-bottom: 5px;
          &:hover {
            color: #337ab7;
          }
        }
      }
    }
    .link,
    .hot {
      font-size: 14px;
      .content {
        padding: 0 15px;
      }
      li {
        height: 40px;
        border-bottom: 1px solid #292929;
        line-height: 40px;

        a {
          text-decoration: none;
          color: #fff;
          &:hover {
            color: #337ab7;
          }
        }
      }
    }
    .link {
      .content {
        padding-top: 20px;
      }
      li {
        width: 50%;
        display: inline-block;
      }
    }
    .tags {
      .content {
        padding: 30px 15px 20px 15px;
      }
      a {
        margin: 0 5px;
        text-decoration: none;
        display: inline-block;
        span {
          cursor: pointer;
        }
      }
    }
  }
  @media screen and (max-width: 1170px) {
    & {
      width: 970px;
    }
  }
  @media screen and (max-width: 970px) {
    & {
      width: 750px;
      .left {
        width: 100%;
        padding-right: 0;
      }
      .right {
        width: 100%;
      }
    }
  }
  @media screen and (max-width: 768px) {
    & {
      width: 95%;
      .left {
        width: 100%;
        padding-right: 0;
      }
      .right {
        width: 100%;
      }
    }
  }
}
</style>
