import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    blogList: {},
    blog: {},
    isShow: false,
    chatUser: { parentUser: "管理员", parentId: -1 }
  },
  mutations: {
    getBlogList(state, result) {
      state.blogList = result;
    },
    getBlog(state, result) {
      state.blog = result[0];
    },
    getIsShow(state, result) {
      state.isShow = result
    },
    getChatUser(state, result) {
      state.chatUser = result;
    }
  },
  actions: {
    setBlogList({ commit }, page = 1, limit = 10) {
      fetch(`/api/blog?page=${page}&limit=${limit}`).then(resp => resp.json()).then(resp => {
        commit('getBlogList', resp.data);
      })
    },
    setTagBlog({ commit }, id) {
      fetch('/api/tags/' + id, {
        method: 'post'
      }).then(resp => resp.json()).then(resp => {
        commit('getBlogList', { data: resp.data[0].blogs })
      })

    },
    setBlog({ commit }, id) {
      fetch('/api/blog/' + id).then(resp => resp.json()).then(resp => {
        commit('getBlog', resp.data);
      })
    }
  },
  modules: {
  }
})
