<template>
  <div class="home">
    <Oneday />
    <div class="articles">
      <Article
        v-for="(item, index) in $store.state.blogList.data"
        :key="index"
        :result="item"
      />
    </div>
    <FandByPage
      v-if="blogList.count"
      :page="page"
      :limit="10"
      :total="blogList.count"
      :pNumber="5"
      @click="findByPageClick"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import Oneday from "../components/home/Oneday";
import Article from "../components/home/Article";
import FandByPage from "../components/compins/FandByPage";
import { mapState, mapMutations, mapActions } from "vuex";
import { socll } from "../utils/index";
export default {
  name: "Home",
  data() {
    return {
      page: 1,
    };
  },
  components: {
    Oneday,
    Article,
    FandByPage,
  },
  mounted() {
    // this.$store.dispatch('setBlog',98);
    // console.log(this);
  },
  computed: {
    ...mapState(["blogList"]),
  },
  methods: {
    findByPageClick(i) {
      if (i === this.page) {
        return;
      }
      this.page = i;
      this.setBlogList(i);
      socll()
    },
    ...mapActions(["setBlogList"]),
  },
};
</script>
<style lang="less">
.articles {
  width: 100%;
}
</style>
