<template>
  <div class="day">
    <p class="title" :style="{ color: randomColor }">
      每日一句({{ text.date }})
    </p>
    <p>{{ text.y }}</p>
    <p class="content">
      <span >{{ text.z }}</span
      ><span class="name">{{ text.name }}</span>
    </p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text: {
        y:
          "When something is important enough,you do it even if the odds are not in your favor",
        z: "",
        name: "",
        date: "",
      },
    };
  },
  props: [],
  computed: {
    randomColor() {
      let r = Math.random() * (255 - 50) + 50;
      let g = Math.random() * (255 - 50) + 50;
      let b = Math.random() * (255 - 50) + 50;
      return `rgb(${r},${g},${b})`;
    },
  },
  mounted() {
    fetch("/api/every?page=1&limit=1")
      .then((resp) => resp.json())
      .then((resp) => {
        let item = resp.data.data[0];
        this.text.name = "————" + item.name;
        this.text.z = item.chatday;
        let date = new Date(item.ctime * 1000);
        let year = date.getFullYear();
        let month = parseInt(date.getMonth()) + 1;
        month = month < 10 ? "0" + month : month;
        let day = date.getDate();
        this.text.date = year + month + day;
      });
  },
};
</script>

<style lang="less">
.day {
  background-color: #f5f5f5;
  box-shadow: 1px 1px 10px #5f5f5f;
  border: 1px solid #e3e3e3;
  box-sizing: border-box;
  padding: 20px;
  font-size: 14px;
  border-radius: 4px;
  margin-bottom: 20px;
  .title {
    font-size: 12px;
    margin-bottom: 10px;
    &::after {
      display: inline-block;
      content: "\e122";
    }
  }
  .content {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    flex-wrap: wrap;
    // .left{
    //   float: left;
    // }
    .name{
      width: 100%;
      text-align: right;
    }
    // .right{
      // margin-top: 60px;
      // float: right;
    // }
  }
}
</style>
