<template>
  <!-- 1.第几页page 2.显示多少 3.总页数 4.每页显示页码总数 5.处理函数-->
  <div class="wrapper-page">
    <div class="inner">
      <span v-if="page !== 1" @click="$emit('click', 1)">首页</span>
      <span v-if="page > 1" @click="$emit('click', page - 1 < 1 ? 1 : page - 1)"
        >上一页</span
      >
      <span
        v-for="i in indexs"
        :key="i"
        :class="{ active: page === i }"
        @click="$emit('click', i)"
        >{{ i }}</span
      >
      <span
        v-if="page < totalPage"
        @click="$emit('click', page + 1 > totalPage ? totalPage : page + 1)"
        >下一页</span
      >
      <span v-if="page !== totalPage" @click="$emit('click',totalPage)"
        >尾页</span
      >
      <span>{{ totalPage }} / {{ page }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["page", "limit", "total", "pNumber"],
  computed: {
    totalPage() {
      return Math.ceil(this.total / this.limit);
    },
    indexs() {
      let arr = [];
      if (this.page <= 3) {
        for (let i = 1; i <= this.pNumber; i++) {
          if (i <= this.totalPage) {
            arr.push(i);
          }
        }
        return arr;
      }

      if (this.page >= this.totalPage - 3) {
        for (let i = this.pNumber - 1; i >= 0; i--) {
          if (this.totalPage - i >= 1) {
            arr.push(this.totalPage - i);
          }
        }
        return arr;
      }
      let cha = Math.floor(this.pNumber / 2);
      let min = this.page - cha;
      let max = min + this.pNumber;
      if (min < 1) {
        min = 1;
      }
      if (max > this.totalPage) {
        max = this.totalPage;
      }

      for (let i = min; i < max; i++) {
        arr.push(i);
      }
      return arr;
    },
  },
};
</script>

<style lang="less">
.wrapper-page {
  position: relative;
  display: inline-block;
  *display: inline;
  *zoom: 1;
  //   border: 1px solid #ddd;
  // padding: 4px 0;
  margin-bottom: 20px;
  .inner {
    // position: absolute;
    // left: 0;
    // top: 0;
    display: flex;
    overflow: hidden;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  span {
    &.active {
      background-color: #191818;
      color: #fff;
    }
    background-color: #fff;
    padding: 6px 12px;
    border-right: 1px solid #ddd;
    font-size: 14px;
    cursor: pointer;
  }
}
</style>
