<template>
  <div class="article">
    <div class="title">
      <a :href="'/details/' + result.id">{{ result.title }}</a>
    </div>
    <div class="content">
      {{ result.introduce }}
    </div>
    <div class="foot">
      <span class="date">发布于{{ date }}|</span>
      <span class="views">浏览（{{ result.views }}）|</span>
      <span class="tags"
        >tags：<a
          @click="tagsClick(i)"
          v-for="(i, index) in tag"
          :key="index"
          >{{ i }}</a
        ></span
      >
    </div>
  </div>
</template>

<script>
import { socll } from "../../utils/index";
export default {
  data() {
    return {
      tags: [],
    };
  },
  props: ["result"],
  //   computed: {
  //     tags(item) {
  //       return this.result.tags.join(" ");
  //     },
  //   },
  computed: {
    date() {
      let date = new Date(this.result.ctime * 1000);
      let year = date.getFullYear();
      let month = +date.getMonth() + 1;
      let day = date.getDate();
      return `${year}年${month}月${day}日`;
    },
    tag() {
      let strs = this.result.tag.replace(/\，/g, ",").split(",");
      // let tags = [];
      // for (let i = 0; i < strs.length; i++) {
      //   await fetch("http://127.0.0.1:12306/api/tags?tag=" + strs[i])
      //     .then((resp) => resp.json())
      //     .then((resp) => {
      //       tags.push(resp.data[0]);
      //     });
      // }
      return strs;
    },
  },
  mounted() {},
  methods: {
    tagsClick(i) {
      fetch("/api/tags?tag=" + i)
        .then((resp) => resp.json())
        .then((resp) => {
          this.$store.dispatch("setTagBlog", resp.data[0].id);
        });
      socll();
    },
  },
};
</script>

<style lang="less">
.article {
  margin-bottom: 20px;
  // padding: 30px 20px 0 20px;
  border: 1px solid transparent;
  box-sizing: border-box;
  border-color: #bfbfbf #8e8e8e #5f5f5f #bfbfbf;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  .title {
    padding: 15px;
    font-size: 20px;
    margin: 12px 0 0px 0;
    a {
      text-decoration: none;
      color: #13102b;
      &:hover {
        color: #23527c;
      }
    }
  }
  .content {
    padding: 0 15px 15px 15px;
    font-size: 14px;
    margin-bottom: 10px;
  }
  .foot {
    padding: 5px 15px;
    color: #eee;
    background-color: #1f1f1f;
    border-top: 1px solid #bfbfbf;
    font-size: 14px;
    .tags {
      a {
        color: #eee;
        text-decoration: none;
        margin-right: 5px;
        cursor: pointer;
      }
    }
  }
}
</style>
