let socll = () => {
    let timer = setInterval(() => {
        window.scrollBy(0, -25);
        if (document.documentElement.scrollTop == 0) {
            clearInterval(timer);
        }
    }, 10);
}

function setComment(self, blogid,fun) {
    fetch("/api/comment/where", {
        method: "post",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({ where: { blogid: blogid } }),
    })
        .then((resp) => resp.json())
        .then((resp) => {
            let items = [];
            items = resp.data.filter((item) => {
                return item.parentid == -1;
            });
            for (const prop of items) {
                prop.children = resp.data.filter((item) => {
                    return prop.id == item.parentid;
                });
            }
            self.comLen = resp.data.length
            self.comment = items;
            self.total = items.length
            if(fun){
                fun();
            }
        });
}



export { socll, setComment };